.sectionContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border: 1px solid black;
  background-color: rgb(216, 214, 214);
  border-radius: 0.2em;
  padding: 0.5em 0;
  /* margin-top: 1em; */
}

.sectionTitleContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-bottom: 1em;
}

.block {
  padding: 2px 0;
  /* text-align: center; */
  /* vertical-align: middle; */
}

.smallPicContainer{
  width: 1em;
  height: 1em;
  padding: 0;
  margin: 0;
}

.smallPic{
  width: 100%;
  height: auto;
  padding: 0;
  margin: 0;
}
